import Image from "next/image";
import Help from "./Help";
import Setting from "./Settings";

export default function Header({ activeTab }: { activeTab: string }) {
    return(
        <div>
            <header className="px-10 py-4 flex justify-between align-items-center border rounded w-[calc(90vw-180px)] mb-5 ml-5 bg-gray-100 border-gray-50 shadow rounded">
                <div>
                    <input className="flex item-center w-100 h-14 outline-none px-8 rounded-4xl bg-white mx-auto" type="text" placeholder="What do you need help with today?"/>
                </div>
                <div className="flex justify-end gap-7">
                    <span className="flex items-center justify-center h-14 w-14 rounded-4xl bg-white hover:cursor-pointer"><i className="fa-regular fa-envelope text-gray-700 text-xl"></i></span>
                                
                    <span className="flex items-center justify-center h-14 w-14 rounded-4xl bg-white hover:cursor-pointer"><i className="fa-regular fa-bell text-gray-700 text-xl"></i></span>
        
                    <span className="flex gap">
                        <span className="flex items-center justify-center h-14 w-14 rounded-4xl bg-white hover:cursor-pointer"><Image className="rounded-4xl" src={"/user.jpg"} width={50} height={50} alt="Prakhar Gupta"/></span>
                        <span className="flex items-center justify-center h-14 w-40 hover:cursor-pointer">
                            <span><p className="font-bold text-lg">Testing User</p><p className="text-base text-gray-500">testing@gmail.com</p></span>
                        </span>
                                    
                    </span>
                </div>
            </header>
            {activeTab === "help" && <Help/>}
            {activeTab === "settings" && <Setting/>}
        </div>
    );
};